import React, { useState } from "react";
const SearchBar = () => {
    const [query, setQuery] = useState(' ');
    const [info, setInfo] = useState()
    
    const getMedication = () => {
        fetch(`https://api.fda.gov/drug/drugsfda.json?api_key=UxVYQb0whi7tcefIKHwPhINOixfCFPc492vBFzYY&search=products.brand_name:${query}&limit=1`)
        .then(res =>res.json())
        .then(data =>  {
            setInfo(data.results[0].products)
        }).catch(err => console.log(err)) 
    };

    const onChange = (e) => {
        setQuery(e.target.value);
    };

    const onSubmit = (e) => {
        e.preventDefault();
        getMedication();
      
    };

    const resultsJsx = info && info.map(med => {
        return (
            <div>
                <h2 className='title'><span className="tag">Drug Name:</span>{med.brand_name}</h2> 
                <h2 className='title'><span className="tag">Generic Name:</span>{med.generic_name}</h2> 
                <h2 className='title'><span className="tag">Drug Class:</span>{med.pharm_class_epc}</h2> 
                <h2 className='title'><span className="tag">Use Case:</span>{med.pharm_class_pe}</h2> 
                <h2 className='title'><span className="tag">Route Type:</span>{med.route}</h2> 
            </div> 
        )
    })      
    return(
        <>
        <form className="searchBox" onSubmit={onSubmit}>
            <input type="text" placeholder="Search Drug" autoComplete='on' onChange={onChange}/>
            <button>Search</button>
        </form>
        <div className="list-items">
            {resultsJsx}
        </div>
       </>
    )
}

export default SearchBar;